@use 'src/styles/vars' as v;

.cross-close {
  border: 0;
  border-radius: calc(v.$round / 2);
  font-size: v.$font-title;
  background-color: v.$color-cross;
  color: white;
  position: absolute;
  padding: 0 4px;
  z-index: 1;
  &:after {
    display: inline-block;
    content: '\00d7'; /* This will render the 'X' */
  }
  &:hover {
    cursor: pointer;
  }
  &-items {
    right: calc(5vw - 1rem);
    margin-top: -30px;
  }
  &-spell {
    right: calc(5vw + 300px);
    margin-top: -30px;
  }
}
