@use 'src/styles/vars' as v;

.background-modal {
  background: rgba(0, 0, 0, 0.4);
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 998;
  display: flex;
  align-items: center;
  justify-content: center;
}
.modal-container {
  background-color: #ffffff;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 500px;
  padding: v.$gutter * 2;
  border-radius: v.$round;
  & h3 {
    font-size: v.$font-title-build;
    padding: v.$gutter;
  }
  & p {
    font-size: v.$font-text;
    padding: v.$gutter;
    padding-bottom: v.$gutter * 2;
  }
  & .action-buttons {
    padding: v.$gutter;
  }
}
.cross-close-modal {
  border: 0;
  background-color: red;
  border-radius: 0.2em;
  color: white;
  position: absolute;
  padding: 2px 6px;
  z-index: 1;
  right: -8px;
  top: -8px;
  &:after {
    display: inline-block;
    content: '\00d7'; /* This will render the 'X' */
  }
}
